import {Component, Element, h, Host, Prop} from '@stencil/core';
import {HostElement} from '@stencil/core/internal';

@Component({
  tag: 'soul-skeleton-key-value',
  styleUrl: 'soul-skeleton-key-value.scss',
  shadow: true,
})
export class SoulSkeletonKeyValue {
  @Prop({reflect: true}) horizontal: boolean = false;
  @Prop({reflect: true}) width: string;
  @Prop({reflect: true}) lines = 1;
  @Element() hostElement: HostElement;

  render() {
    if (this.width) {
      this.hostElement.style.setProperty('width', this.width);
    }

    return (
      <Host>
        <div class={{'soul-skeleton-key-value': true, 'soul-skeleton-key-value--horizontal': this.horizontal}}>
          <soul-skeleton-label width="40%"></soul-skeleton-label>
          <soul-skeleton-text lines={this.lines}></soul-skeleton-text>
        </div>
      </Host>
    );
  }

}
